import React from 'react';
import Callapi from './../ultils/apiCaller';
class AddCate extends React.Component{
      constructor(props){
        super(props);
        this.state={
          id:'',
          name:'',
          status:'',
          cateId:''
        };
      }
      onChange=(e)=>{
        var target=e.target;
        var name=target.name;
        var value =target.value;
        this.setState({
          [name]:value
         });
      }
      Onsave=(e)=>{
         e.preventDefault();
         var {name, status,cateId}=this.state;
        Callapi(`categories/${cateId}/cate`,'POST',{
          categoryId:cateId,
          cateName:name,
          status:status
        }).then(res=>{
          console.log(res);
        })
        this.setState({
          id:'',
          name:'',
          status:'',
        });
      }
	render() {
    var {name,status,cateId}=this.state;
    console.log(cateId);
		return (
      <div>
      <div className="card">
        <div className="card-heading">
          <div className="card-title ">Thm danh mc</div>
        </div>
           </div>
        <div className="card-body">
        <form onSubmit={this.Onsave}>
        <div className="form-group">
          <label>Tn danh mc</label>
          <input className="form-control" 
          type="text" 
          name="name" 
          placeholder="Tn danh mc"
          value={name}
          onChange={this.onChange}
          ></input>
          <td><select 
          name='status'
          onChange={this.onChange}
          value={status}
          >
              <option value={false}>n</option>
              <option value={true}>Hot ng</option>
            </select></td>
           <td><select 
          name='cateId'
          onChange={this.onChange}
          value={cateId}
          >
              <option value={1}>PHN PHI IN H TH</option>
              <option value={2}>IU KHIN V T NG HA</option>
               <option value={3}>THIT B TRUNG TH</option>
              <option value={4}>CP IN,THANH CI,PH KIN U NI</option>
               <option value={5}>THIT B CHIU SNG</option>
              <option value={6}>B LU IN UPS</option>
            </select></td> 
         </div>
        <button type="submit" className="btn btn-primary">Thm</button>&nbsp;
        <button type="button"
         className="btn btn-danger"> Hy</button>
        </form>
        </div>
        </div>
        
		)
	}
}
export default AddCate;